import React from 'react';
import { Item } from './item/Item';

export const Footer = ({ onAll, onActive, onCompleted, onClearAll }) => {

  return (
    <footer className="footer">
       <span className="todo-count">
          3 items left
       </span>

      <ul className="filters">
        <Item link='/all' text='all' run={onAll} />
        <Item link='/active' text='active' run={onActive} />
        <Item link='/completed' text='completed' run={onCompleted} />
      </ul>

      <button
        onClick={onClearAll}
        type="button" className="clear-completed">
        Clear completed
      </button>
    </footer>
  );
};
